body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.deck {
  display: -webkit-flex;
  display: flex;
  border: 1px solid black;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 19vh;
  padding: 2vh;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 18vh;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.reserved-deck {
  height: 21vh;
  width: 15vh;
  border: 1px solid;
  position: relative;
}

.reserved-deck > div {
  position: absolute;
}

.pile-card {
  position: absolute;
}

.horizontal-layer {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.piles {
  display: -webkit-flex;
  display: flex;
}

.card {
  height: 19vh;
  width: 15vh;
  font-size: 21vh;
  line-height: 0.7;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: white;
  border: black 1px solid;
}

.pile {
  min-height: 20vh;
  width: 20vh;
  margin: 1vh;
  position: relative;
}

body {
  padding: 1vh;
}

.tableau {
  margin-top: 2vh;
}

